@import url('https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:400,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');

body {
  font-family: "Open Sans", serif;
  font-size: 30px;
}

 .wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
 .container{
    text-align: center;
}  
.leftJcontainer{
  text-align: start;
  color: #49aee9;
  /* color: white; */
  margin: 20px;
  /* padding: 10px; */
}

.main {
  background: #fff;
  width: 80%;
  margin: 0 auto;
}

.form-container {
  background-color: #FFFFFF; 
  width: 100%;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  font-size:1em;
  cursor: pointer;
  background-color: #49aee9;
  color: #fff;
  font-weight: 100;
}

.weather__info {
  width: 80%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  /* color: black; */
  font-weight: 400;
}


.weather__error {
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}